import React from "react";
import "./App.css";

export default function Team({ team }) {
  return (
    <div className="body">
      <div className="rank">
        <div className="rank-num">{team.rank}</div>
        <div className="rank-team">
          <img src={team.url}></img>
          <div>{team.name}</div>
        </div>
      </div>
      <div className="game">{team.game}</div>
      <div className="win">{team.win}</div>
      <div className="draw">{team.draw}</div>
      <div className="lose">{team.lose}</div>
      <div className="win-gap">{team.winGap}</div>
      <div className="win-rate">{team.winRate}</div>
      <div className="recently">{team.recently}</div>
    </div>
  );
}
