import axios from "axios";

const path = "/TeamRank";

export const makeRequest = (path) =>
  axios.get(`https://lgukbojson.sports2i.com/ws/KBORank.asmx${path}`, {
    params: {
      nickname: "test",
      season: "2020",
      tag: "0",
    },
  });

export const kboAPI = {
  rankRequest: () => makeRequest(path),
};

